.section {
	position: relative;
	background-size: cover;
	background-repeat: no-repeat;
	margin: 0;
	padding: 0;
	height: 100%;
	width: 100%;
}

.nav {
	/* background-color: #212121; */
	background: url('https://cdn.fs.teachablecdn.com/RD4lJ0jZTq6k6zfSQ8de')
		fixed;
	color: #fff;
	transition: all 0.5s ease;
	font-size: 12px;
	font-family: 'Open Sans', sans-serif;
	width: 100%;
	z-index: 100;
	position: absolute;
	left: 0;
	letter-spacing: 2px;
	line-height: 100px;
	/* margin-bottom: 10vh; */
	transition-property: background-color, box-shadow, line-height, height;
	transition-timing-function: cubic-bezier(0.78, 0.13, 0.15, 0.86);
	transition-duration: 0.3s;
	height: 12vh;
	/*max-height: auto; */
}

.nav .brand {
	height: 12vh;
	line-height: 100px;
	padding-left: 30px;
	display: inline-block;
	float: left;
	font-size: 20px;
	font-family: 'Pacifico', cursive;
	transition-property: background-color, box-shadow, line-height, height;
	transition-timing-function: cubic-bezier(0.78, 0.13, 0.15, 0.86);
	transition-duration: 0.3s;
	/* padding-right: 60px; */
}
.headerLogo {
	margin-top: 25px;
	height: 40px;
}

.nav .brand a {
	color: #e33b00;
	text-decoration: none;
	font-size: 36px;
}
.nav ul {
	margin: 0;
	text-transform: uppercase;
	height: 12vh;
	padding: 0;
}
.nav ul li {
	text-align: center;
	display: inline-block;
	list-style: none;
	padding: 5px 15px;
	cursor: pointer;
	line-height: 30px;
}
.nav ul li:hover a {
	font-weight: bold;
}
.nav ul li a {
	color: #eee;
	text-decoration: none;
}
.menu {
	display: none;
}
@media (max-width: 950px) {
	.nav ul li {
		padding: 5px;
	}
}

@media (max-width: 800px) {
	.sticky .menu {
		top: 0;
	}
	.sticky .menu .hamburger {
		background: #6e7982;
	}
	.sticky .menu .hamburger::before,
	.sticky .menu .hamburger::after {
		background: #6e7982;
	}

	.open.sticky .hamburger {
		background: transparent;
	}

	.open .hamburger {
		background-color: transparent;
	}
	.open .hamburger::before {
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
	}
	.open .hamburger::after {
		-webkit-transform: rotate(-45deg) translate(2px, -2px);
		transform: rotate(-45deg) translate(2px, -2px);
	}

	.menu {
		display: block;
		outline: none;
		position: relative;
		line-height: 60px;
		float: left;
		left: 20px;
		top: 20px;
		width: 60px;
		height: 60px;
		background: none;
		border: 0;
		padding: 0;
		margin: 0;
		cursor: pointer;
		opacity: 0.7;
		transition: opacity 150ms;
	}
	.menu:hover {
		opacity: 1;
	}

	.hamburger,
	.hamburger::after,
	.hamburger::before {
		margin: 0 auto;
		display: block;
		width: 24px;
		height: 3px;
		line-height: 0;
		transition: all 150ms;
	}

	.hamburger::before {
		content: '';
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
		background: #fff;
	}

	.hamburger::after {
		content: '';
		-webkit-transform: rotate(-45deg) translate(2px, -2px);
		transform: rotate(-45deg) translate(2px, -2px);
		background: #fff;
	}

	.hamburger {
		background: #fff;
	}
	.hamburger::after {
		-webkit-transform: translateY(5px);
		transform: translateY(5px);
	}
	.hamburger::before {
		-webkit-transform: translateY(-8px);
		transform: translateY(-8px);
	}

	.collapse nav {
		-webkit-transform: translateY(-100%);
		transform: translateY(-100%);
	}
	.navbar {
		transition: -webkit-transform 150ms;
		transition: transform 150ms;
		transition: transform 150ms, -webkit-transform 150ms;
	}

	ul.navbar {
		-webkit-transform: translate(-100%, 0);
		transform: translate(-100%, 0);
		/* transform: translateY(-200%); */
		padding-left: 0;
	}
	ul.navbar li {
		/* line-height: calc((100vh - 60px) / 6); */
		border-bottom: 1px solid;
		display: block;
	}

	.open .navbar {
		-webkit-transform: translate(0, 0);
		transform: translate(0, 0);
	}

	.nav .brand {
		display: block;
		text-align: center;
		float: none;
	}

	.sticky .brand {
		background-color: white;
		box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
	}

	nav {
		height: 100px;
	}
	nav.open {
		height: auto;
		min-height: 70%;
	}
	nav.sticky {
		height: 60px;
	}
	nav .open.sticky {
		height: auto;
	}
	.dropdown-icon :after {
		content: '▾' !important;
	}
}

